<template>
  <v-container class="container" fluid>
    <Header />
    <Toolbar />
    <v-layout class="generalPic"></v-layout>
    <v-layout class="mastClassText">
      <p>
        На мастер-классах вы будете находиться на своей «личной территории», с индивидуальным оборудованием, аксессуарами и подготовленными специально для Вас продуктами.
        <span
          v-if="showText"
        >
          Никаких «общих котлов»! Шеф-повар уделит внимание абсолютно каждому участнику и понятно расскажет, и покажет все, что «не видно между строк» в кулинарных книгах и программах, ответит на все возникшие вопросы и вручит именной диплом о прохождении мастер-класса.
          По окончании мастер-класса участники собираются за большим столом, чтобы насладиться приготовленной едой. Каждый мастер-класс – это полное погружение в процесс! Вы убедитесь, что приготовление вкусной, яркой и полезной еды – это не МУ́КА, а интересный процесс, который может доставлять удовольствие.
        </span>
        <button @click="showTextContent()" class="arrowRight">
          <v-icon dark v-if="!showText">reply_all</v-icon>
        </button>
        <button @click="hideTextContent()" class="arrowLeft">
          <v-icon dark v-if="showText">reply_all</v-icon>
        </button>
      </p>
    </v-layout>
    <v-layout class="products">
      <v-layout wrap align-center class="filterWrapper">
        <v-flex xs12 sm6 md3 class="filterLine">
          <v-select
            class="filter"
            :items="['Выбрать стоимость', 'От 1000 до 3000', 'От 3000 до 5000', 'От 5000 до 6000']"
            v-model="type"
            @change="filterByPrice"
            suffix
          ></v-select>
        </v-flex>
        <v-flex xs12 sm6 md3 class="filterLine">
          <v-select
            class="filter"
            :items="kitchen"
            v-model="type1"
            @change="filterByKitchen"
            suffix
          ></v-select>
        </v-flex>
        <v-flex xs12 sm6 md3 class="pickerLine">
          <v-menu
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
            class="menu"
          >
            <template v-slot:activator="{ on }">
              <v-text-field :value="transformDate" prepend-icon="event" readonly v-on="on"></v-text-field>
            </template>
            <!-- <v-btn class="refresh" @click="refresh()">Сбросить дату</v-btn> -->
            <v-date-picker
              v-model="date"
              @input="menu = false"
              locale="ru"
              @change="showMasterClass"
            ></v-date-picker>
          </v-menu>
        </v-flex>
      </v-layout>
      <MasterClasses
        :masterClasses="MasterClasses"
        v-if="show"
        :search="this.search"
        :showModal="showModal"
      />
      <MasterClasses :masterClasses="filtred" v-if="!show" :search="this.search" />
      <MasterClasses :masterClasses="filteredItems" v-if="filter" :search="this.search" />
    </v-layout>
    <Partners />
    <MobContacts />
    <Footer />
  </v-container>
</template>

<script>
import Header from "../components/Header";
import Toolbar from "../components/Toolbar";
import Partners from "../components/Partners";
import Footer from "../components/Footer";
import MasterClasses from "../components/MasterClasses";
import MobContacts from "../components/MobContacts";

import moment from "moment";

export default {
  name: "MasterClassesPage",
  components: {
    Header,
    Toolbar,
    Partners,
    MasterClasses,
    MobContacts,
    Footer
  },
  data: () => ({
    type: "Выбрать стоимость",
    type1: "Выбрать кухню",
    search: "",
    date: new Date().toISOString().substr(0, 10),
    menu: false,
    modal: false,
    filtred: [],
    show: true,
    filter: false,
    cartItems: [],
    a: "",
    showText: false
  }),
  computed: {
    MasterClasses() {
      return this.$store.getters.masterClasses;
    },

    Cart() {
      return this.$store.getters.cart;
    },

    transformDate: function() {
      return this.date
        ? moment(this.date)
            .locale("ru")
            .format("LL")
        : "";
    },

    kitchen() {
      return this.$store.getters.kitchen;
    }
  },

  created() {
    //  this.$store.dispatch("loadCart");
    this.$store.dispatch("masterClassesList");
    this.$store.dispatch("getKitchenTypes");
  },

  methods: {
    showTextContent() {
      this.showText = true;
    },

    hideTextContent() {
      this.showText = false;
    },

    showModal(item) {
      this.$store.dispatch("showDetails", item);
      const parsed = JSON.stringify(this.MasterClasses);
      localStorage.setItem("MasterClasses", parsed);
    },

    showMasterClass() {
      let newDate = this.date;

      function containsElem(element) {
        return element.dateofevent.substr(0, 10) !== newDate;
      }

      if (this.MasterClasses.every(containsElem)) {
        this.show = false;
        this.filtred.splice(0);
      }

      this.MasterClasses.forEach(e => {
        if (e.dateofevent.substr(0, 10) === newDate) {
          this.show = false;
          if (!this.filtred.includes(e)) {
            this.filtred.push(e);
          }
          this.filtred.forEach(i => {
            if (!i.dateofevent.substr(0, 10) === newDate) {
              this.filtred.splice(0);
              this.filtred.push(e);
            }
          });
        }
      });
    },

    filterByPrice() {
      let typeS = this.type;
      let arr;
      let arr2 = [];
      arr = this.MasterClasses;
      switch (typeS) {
        case "От 1000 до 3000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 3000 && c.price >= 1000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "От 3000 до 5000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 5000 && c.price >= 3000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "От 5000 до 6000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 6000 && c.price >= 5000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "Выбрать стоимость":
          this.show = true;
          break;
        default:
          break;
      }
    },

    filterByKitchen() {
      let typeS = this.type1;
      let arr;
      let arr2 = [];
      arr = this.MasterClasses;
      this.show = false;
      arr.forEach(elem => {
        if (elem.kitchen.name === typeS && typeS !== "Выбрать кухню") {
          arr2.push(elem);
        }
        this.filtred = arr2;
      });

      if (typeS === "Выбрать кухню") {
        this.show = true;
      }
    },

    refresh() {
      this.show = true;
      this.menu = false;
    }
  }
};
</script>

<style>
.arrowRight, .arrowLeft {
  width: 2vw;
}

.arrowRight .v-icon.v-icon {
  transform: scale(-1, 1);
  color: rgba(171, 112, 214) !important;
  font-size: 1.5vw;
}

.arrowLeft .v-icon.v-icon {
  color: rgba(171, 112, 214) !important;
  font-size: 1.5vw;
}

span {
  font-family: "Roboto", sans-serif;
}
.refresh {
  color: #ffffff !important;
  z-index: 3;
  box-shadow: none !important;
  background-color: rgba(171, 112, 214) !important;
  border-radius: 0;
  height: 0 !important;
  justify-content: flex-end;
}

.refresh .v-btn__content {
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: center;
  /* background-color: white; */
  padding-top: 0vw;
  height: 1.5vw;
  width: 9vw;
  padding-left: 0vw;
  max-width: 5vw;
  margin-top: 0.6vw;
}

.refresh .v-btn__content:hover {
  background-color: rgba(240, 238, 241, 0.4);
}

.refresh .v-btn__content:active {
  transform: translateY(1px);
  filter: saturate(150%);
}

.refresh:before {
  background-color: transparent !important;
  display: none;
}

.refresh:focus {
  outline: none;
}

.menu .v-menu__content {
  display: flex;
  flex-direction: column;
}

.theme--light.v-btn.v-btn--icon {
  background-color: rgba(137, 90, 172, 0.4);
}

.mastClassText {
  margin-top: 2.5vw;
  flex-direction: column;
}

.mastClassText p {
  text-align: center;
  max-width: 90%;
  font-family: "Helvetic-light", sans-serif;
}

.products {
  flex-direction: column;
  margin-top: 3vw;
}

.filterWrapper {
  background-color: rgba(137, 90, 172, 0.4);
  width: 80%;
  margin: 0 auto;
  justify-content: space-between;
}

.filterWrapper .v-picker__title {
  color: white;
  background-color: rgba(171, 112, 214);
}

.filter .v-list-item .v-list-item__title {
  font-size: 1.4vw;
}

.filterLine {
  padding-left: 1%;
}

.pickerLine {
  padding-right: 1%;
}

.filter .v-menu__content {
  top: 0px !important;
  left: 0px !important;
  transform-origin: left top;
  z-index: 8;
}

.filterWrapper .theme--light.v-icon,
.filterWrapper .theme--light.v-select .v-select__selections {
  color: white;
  font-size: 1.5vw;
}

.filterWrapper
  .v-text-field
  > .v-input__control
  > .v-input__slot
  > .v-text-field__slot {
  border-bottom: 1px solid white;
}

.filterWrapper .theme--light.v-input:not(.v-input--is-disabled) input {
  color: white;
  font-size: 1.5vw;
  text-indent: 1vw;
}

.filterWrapper .v-select__slot {
  border-bottom: 1px solid white;
}

.pickerLine .v-date-picker-table .v-btn.v-btn--active {
  background-color: rgba(171, 112, 214);
}

@media (min-width: 320px) and (max-width: 500px) {
  .filterWrapper .theme--light.v-icon,
  .filterWrapper .theme--light.v-select .v-select__selections {
    font-size: 4vw;
  }

  .filterWrapper .theme--light.v-input:not(.v-input--is-disabled) input {
    color: white;
    font-size: 4vw;
    text-indent: 1vw;
  }

  .filter .v-list-item .v-list-item__title {
    font-size: 3.7vw;
  }
}

@media (min-width: 501px) and (max-width: 700px) {
  .filterWrapper .theme--light.v-icon,
  .filterWrapper .theme--light.v-select .v-select__selections {
    font-size: 2.7vw;
  }

  .filterWrapper .theme--light.v-input:not(.v-input--is-disabled) input {
    color: white;
    font-size: 2.7vw;
    /* font-size: 1.5vw; */
    text-indent: 1vw;
  }

  .filter .v-list-item .v-list-item__title {
    font-size: 2.8vw;
  }
}

@media (min-width: 701px) and (max-width: 1024px) {
  .filterWrapper .theme--light.v-icon,
  .filterWrapper .theme--light.v-select .v-select__selections {
    font-size: 2vw;
  }

  .filterWrapper .theme--light.v-input:not(.v-input--is-disabled) input {
    color: white;
    font-size: 2vw;
    text-indent: 1vw;
  }

  .filter .v-list-item .v-list-item__title {
    font-size: 2vw;
  }
}

@media screen and (max-width: 991px) {
  .filterWrapper {
    width: 91%;
  }
}

@media (min-width: 600px) {
  .flex.sm6 {
    -ms-flex-preferred-size: 50%;
    flex-basis: 40%;
    -webkit-box-flex: 0;
    -ms-flex-positive: 0;
    max-width: 30%;
  }
}
</style>


