<script>
// Подключение компонентов
import Header from "../components/Header";
import Toolbar from "../components/Toolbar";
import MasterClasses from "../components/MasterClasses";
import Sertificate from "../components/Sertificate";
import Contacts from "../components/Contacts";
import Partners from "../components/Partners";
import Footer from "../components/Footer";
import MobContacts from "../components/MobContacts";

export default {
  name: "app",
  components: {
    Header,
    Toolbar,
    MasterClasses,
    Sertificate,
    Contacts,
    Partners,
    Footer,
    MobContacts
  },
  data: () => ({}),
  methods: {},

  computed: {
    MasterClassesHomePage() {
      return this.$store.getters.masterClassesHomePage; // Рендеринг мастер классов из апи для главной страницы
    }
  },

  created() {
    this.$store.dispatch("masterClassesListHomePage"); // включение отображения мастер-классов сразу после загрузки страницы
  }
};
</script>

