
<template>
  <b-modal id="fakePage">
    <div class="forLogo">
      <img :src="logo" alt="#" class="logoFake" />
    </div>
    <h4>Страница находится в разработке</h4>
    <p>Оставьте Ваши контактные данные и наш менеджер свяжется с Вами</p>
    <div class="logInMdDiv">
      <v-text-field v-model="name" :rules="[rules.name, rules.nameRules]" placeholder="Имя*" class="name" required></v-text-field>
      <v-text-field
        v-model="serName"
        :rules="[rules.name, rules.nameRules]"
        placeholder="Фамилия"
        class="sername"
        required
      ></v-text-field>
      <v-text-field class="phone" placeholder="Телефон*" v-model="phone" :rules="[rules.phone]"></v-text-field>
      <v-text-field
        class="email"
        placeholder="Email*"
        v-model="email"
        :rules="[rules.required, rules.email]"
      ></v-text-field>
      <v-textarea outline class="mytextArea" name="input-7-1" placeholder="Укажите Ваш вопрос..." v-model="text"></v-textarea>
      <v-card-actions>
        <v-btn text @click="$bvModal.hide('fakePage'), submit()" class="logInMdBtn">Отправить</v-btn>
      </v-card-actions>
      <div class="connectionIcons">
        <a href="https://www.instagram.com/studio_myka/" target="_blank" class="MobLink">
          <img :src="groupInst" class="instIcon" />
        </a>
        <a href="http://www.facebook.com/studiomyka/" target="_blank" class="MobLink">
          <img :src="iconFace" class="faceIcon" />
        </a>
        <a href="tel:+74951233799" class="MobLink">
          <img :src="phoneIcon" class="phoneIcon" />
        </a>
        <a href="mailto:office@studiomyka.ru" class="MobLink">
          <img :src="mailIcon" class="mailIcon" />
        </a>
      </div>
    </div>
  </b-modal>
</template>

<script>
import logo from "../img/logoFake1.png";

import phoneIcon from "../img/iconsPhoneTube.png";
import mailIcon from "../img/icMail.png";

import iconFace from "../img/faceIc.png";
import groupInst from "../img/groupInst.png";
export default {
  name: "fake",
  data: () => ({
    iconFace: iconFace,
    groupInst: groupInst,
    phoneIcon: phoneIcon,
    mailIcon: mailIcon,
    logo: logo,
    serName: "",
    text: "",
    rules: {
      required: value => !!value || "E-mail обязателен",
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Неверный E-mail";
      },
      phone: value => {
        const pattern = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/;
        return pattern.test(value) || "Неверный формат номера";
      },
      name: value => {
        const pattern = /^[a-zA-Zа-яА-Я]+$/ui;
        return pattern.test(value) || "Неверный формат";
      },
      nameRules: v => !!v || "Введите имя",
    },
    name: "",
    email: "",
    phone: "",
  }),

  methods: {
    submit() {
      this.$store.dispatch("saveRequest", {
        first_name: this.name,
        last_name: this.serName,
        phone_number1: this.phone,
        email: this.email,
        petition: this.text // TODO: update me
      });
    }
  }
};
</script>

